export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VBadge\n        :bordered=\"bordered\"\n        :elevated=\"elevated\"\n        :round=\"round\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :dark=\"dark\"\n        :light=\"light\"\n        :color=\"color\"\n        :size=\"size\"\n        :disabled=\"disabled\"\n      >\n        Configure me\n      </VBadge>\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"dark\"\n          label=\"Dark\"\n        />\n        <KnobBoolean\n          v-model=\"light\"\n          label=\"Light\"\n        />\n        <KnobListItem\n          v-model=\"color\"\n          :items=\"colors\"\n          label=\"Color\"\n        />\n        <KnobListItem\n          v-model=\"size\"\n          :items=\"sizes\"\n          label=\"Size\"\n        />\n        <KnobBoolean\n          v-model=\"bordered\"\n          label=\"Bordered\"\n        />\n        <KnobBoolean\n          v-model=\"elevated\"\n          label=\"Elevated\"\n        />\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"roundedLg\"\n          label=\"Rounded large\"\n        />\n        <KnobBoolean\n          v-model=\"round\"\n          label=\"Round\"\n        />\n        <KnobBoolean\n          v-model=\"disabled\"\n          label=\"Disabled\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean\n} from '@/docs/components';\nimport {\n  VBadge\n} from '@/components';\nimport { colors, sizes } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'BadgeExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    VBadge\n  },\n  data () {\n    return {\n      colors,\n      sizes,\n      bordered: false,\n      elevated: false,\n      round: false,\n      rounded: false,\n      roundedLg: false,\n      dark: true,\n      light: false,\n      color: 'primary',\n      size: 'md',\n      disabled: false\n    };\n  }\n};\n</script>\n";